<template>
  <div class="small-timer">
    <slot />
    {{seconds}}s left
  </div>
</template>

<script>
export default {
  data() {
    return {
      seconds: 60
    }
  },
  mounted() {
    this.interval = setInterval(() => {
      this.seconds--;
      if (this.seconds <= 0) {
        clearInterval(this.interval);
      }
    }, 1000);
  }
}
</script>

<style lang="scss" scoped>
.small-timer {
  width: 100%;
  text-align: center;
}
</style>
