<template>
  <div class="app" :class="{'timer-running': isTimerRunning}">
    <transition mode="out-in" appear>
      <div
        v-if="!this.currentPlayer"
        class="section"
      >
        <Login

          @login="onLogin($event)"
          :players="players"
          class="section"
        />
      </div>
      <div
        v-else
        class="section section-main"
      >
      <WordGenerator
        class="generator"
        @select="onSelectWord"
        :isTimerRunning="isTimerRunning"
        :currentPlayer="currentPlayer"
        :activePlayerId="activePlayerId"
        :isFinished="isFinished"
      />
      <Timer
        class="timer"
        v-if="isTimerRunning"
        @finish="onFinish"
      />
      <SmallTimer
        class="small-timer"
        v-if="otherPlayerPlaying"
      >
        {{otherPlayerPlaying}} is currently playing.
      </SmallTimer>
      <Scores
        :teams="teams"
        :activePlayerId="activePlayerId"
      />
    </div>
    </transition>
    <div class="animation">
      <Background />
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import Login from '@/components/Login'
import WordGenerator from '@/components/WordGenerator'
import Timer from '@/components/Timer'
import Background from '@/components/Background'
import Scores from '@/components/Scores'
import SmallTimer from '@/components/SmallTimer'

export default {
  components: {
    Login,
    WordGenerator,
    Timer,
    Background,
    Scores,
    SmallTimer
  },
  data() {
    return {
      teams: [],
      email: '',
      isLoggedIn: false,
      isTimerRunning: false,
      otherPlayerPlaying: null,
      isFinished: false,
      activePlayerId: null,
      selectedWord: {
        word: null,
        difficulty: null
      }
    }
  },
  computed: {
    players() {
      let players = [];
      this.teams.forEach(team => {
        players = [...players, ...team.players]
      });
      return players;
    },
    currentPlayer() {
      return this.players.find(player => player.email === this.email);
    }
  },
  methods: {
    getData() {
      axios.get('/api/teams/').then(response => {
        this.teams = response.data;

        const activePlayer = this.players.find(player => player.active);
        this.activePlayerId = activePlayer.id;

        if (activePlayer.playing) {
          this.otherPlayerPlaying = activePlayer.name;
        } else {
          this.otherPlayerPlaying = null;
        }
      })
    },
    onLogin(email) {
      this.email = email;

      localStorage.setItem('szPictionaryEmail', email);
    },
    onSelectWord(word) {
      this.isTimerRunning = true;
      this.selectedWord = word;

      axios.post('/api/set_playing/');
    },
    onFinish() {
      this.isTimerRunning = false;
      this.isFinished = true;

      let score;
      if (this.selectedWord.difficulty === 'easy') {
        score = 1;
      }
      if (this.selectedWord.difficulty === 'medium') {
        score = 2;
      }
      if (this.selectedWord.difficulty === 'hard') {
        score = 3;
      }

      axios.post('/api/save_word/', {
        word: this.selectedWord.word,
        score,
        player_id: this.currentPlayer.id
      });
    }
  },
  created() {
    const email = localStorage.getItem('szPictionaryEmail');
    if (email) {
      this.email = email;
    }

    this.getData();
    setInterval(this.getData, 3000);
  }
}
</script>

