<template>
  <div :class="['base', {'timer-running': isTimerRunning, 'finished': isFinished, 'is-generated': isGenerated}]">
    <div class="wrap wrap-intro">
      <div v-if="!isGenerated">
        Hi <span class="name">{{currentPlayer.name}}</span>! Your words will be shown here when it's your turn.
      </div>
      <div v-else-if="!isFinished">
        Hi <span class="name">{{currentPlayer.name}}</span>! It's your turn!
      </div>
      <div v-else>
        Well done! New words will be shown here when it's your turn again.
      </div>
    </div>
    <div class="wrap wrap-words">
      <div class="word-wrap" v-for="(value, key) in words" :key="key">
        <div>
          {{ key }}
        </div>
        <div class="word" :class="{'is-selected': selected && value === selected}" @click="value ? onClick(value) : null">
          {{ value || '?' }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import easy from '@/assets/words/easy.json'
import medium from '@/assets/words/medium.json'
import hard from '@/assets/words/hard.json'

const words = { easy, medium, hard}

export default {
  props: {
    isTimerRunning: Boolean,
    activePlayerId: Number,
    isFinished: Boolean,
    currentPlayer: {
      type: Object,
      default: () => ({
        id: null
      })
    }
  },
  data() {
    return {
      words: {
        easy: '',
        medium: '',
        hard: ''
      },
      selected: '',
      isGenerated: false
    }
  },
  watch: {
    activePlayerId: {
      handler() {
        if (this.activePlayerId === this.currentPlayer.id && !this.isGenerated) {
          this.generateWords();
        } else if (this.activePlayerId !== this.currentPlayer.id && !this.isTimerRunning && this.isGenerated) {
          this.resetWords();
        }
      },
      immediate: true
    }
  },
  methods: {
    generateWords() {
      if (this.isTimerRunning) {
        return;
      }

      this.selected = '';
      this.isGenerated = true;

      Object.keys(words).forEach(key => {
        this.words[key] = words[key][Math.ceil(Math.random() * words[key].length) - 1]
      })
    },
    resetWords() {
      this.words = {
        easy: '',
        medium: '',
        hard: ''
      }
    },
    onClick(word) {
      if (this.selected) {
        return;
      }

      this.selected = word;

      let difficulty;
      Object.keys(this.words).forEach(key => {
        if (word === this.words[key]) {
          difficulty = key
        }
      })
      this.$emit('select', { word, difficulty });
    }
  }
}
</script>

<style lang="scss" scoped>
.word {
  background: transparent;
  padding: 1rem 1rem;
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
  font-size: 2rem;
  outline: none;
  box-shadow: 15px 28px 24px -16px rgba(0, 0, 0, 0.2);
  border-radius: 255px 15px 225px 15px/15px 225px 15px 255px;
}

.wrap {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 1rem;
  text-align: center;
}

.name {
  text-transform: capitalize;
}

.word {
  margin: 1rem;
  min-width: 74px;
  border: dashed 5px #41403E;

  &.is-selected {
    border-color: #fd6e72;
  }

  .base:not(.timer-running):not(.finished) & {
    cursor: pointer;

    &:hover {
      transform: scale(.95);
      box-shadow: 2px 8px 8px -6px rgba(0, 0, 0, 0.3);
    }
  }

  .timer-running & {
    &:not(.is-selected) {
      opacity: .6;
    }
  }
}

.word-wrap {
  transition: all .5s ease;

  &:nth-child(2) {
    transition-delay: .1s;
  }

  &:nth-child(3) {
    transition-delay: .2s;
  }

  .base:not(.is-generated) & {
    transform: scale(.2);
    opacity: 0;
  }
}

.wrap-intro {
  margin-bottom: 3rem;
  -webkit-transition: all .5s ease;
  transition: all .5s ease;

  .timer-running & {
    opacity: 0;
  }
}

.wrap-words {
  transition: all .5s ease;

  .base:not(.is-generated) & {
    margin-top: -90px;
  }
}
</style>
