<template>
  <div>
    <div>
      <div class="intro">
        Please enter your e-mail address
      </div>
      <form @submit.prevent="onSubmit" class="form">
        <input
          v-model="email"
          type="email"
          class="input"
          required
        >
        <Button>
          submit
        </Button>
      </form>
      <div class="error" v-if="showError">
        Unknown e-mail address.
      </div>
    </div>
  </div>
</template>

<script>
import Button from './Button'

export default {
  components: {
    Button
  },
  props: {
    players: {
      type: Array,
      default: () => ([])
    }
  },
  data() {
    return {
      email: '',
      showError: false,
    }
  },
  watch: {
    email() {
      this.showError = false;
    }
  },
  methods: {
    onSubmit() {
      this.players.find(player => player.email === this.email) ? this.$emit('login', this.email) : this.showError = true;
    }
  }
}
</script>

